import { useEffect, useRef, useState } from "react";
import { Rnd } from "react-rnd";

interface Props {
  imageSrc: string;
}

const ASPECT_RATIO = 4 / 5;
const BOX_COUNT = 3;

export default function ImageCropper({ imageSrc }: Props) {
  const containerRef = useRef<HTMLDivElement>(null);
  const [imgDimensions, setImgDimensions] = useState({ width: 0, height: 0 });

  const [boxes, setBoxes] = useState(
    Array.from({ length: BOX_COUNT }, (_, i) => ({
      x: i * 120,
      y: 50,
      width: 120,
      height: 120 / ASPECT_RATIO,
    }))
  );

  const handleBoxChange = (index: number, data: any) => {
    const newBoxes = [...boxes];
    newBoxes[index] = {
      ...newBoxes[index],
      ...data,
    };
    setBoxes(newBoxes);
  };

  return (
    <div className="relative mt-4" ref={containerRef}>
      <img
        src={imageSrc}
        alt="Uploaded"
        onLoad={(e) => {
          const { width, height } = e.currentTarget;
          setImgDimensions({ width, height });
        }}
        style={{ width: "100%", maxWidth: "800px" }}
      />

      {/* Overlay box layer */}
      <div
        className="absolute top-0 left-0"
        style={{
          width: imgDimensions.width,
          height: imgDimensions.height,
        }}
      >
        {boxes.map((box, i) => (
          <Rnd
            key={i}
            size={{ width: box.width, height: box.height }}
            position={{ x: box.x, y: box.y }}
            onDragStop={(_, d) => handleBoxChange(i, { x: d.x, y: d.y })}
            onResizeStop={(_, __, ref, ___, pos) =>
              handleBoxChange(i, {
                width: parseFloat(ref.style.width),
                height: parseFloat(ref.style.height),
                x: pos.x,
                y: pos.y,
              })
            }
            lockAspectRatio={ASPECT_RATIO}
            bounds="parent"
            style={{
              border: "2px solid red",
              zIndex: 10,
              position: "absolute",
            }}
          />
        ))}
      </div>

      {/* Crop + export */}
      <button
        className="mt-4 px-4 py-2 bg-blue-500 text-white rounded"
        onClick={async () => {
          const img = new Image();
          img.src = imageSrc;
          await img.decode();

          boxes.forEach((box, i) => {
            const canvas = document.createElement("canvas");
            const scaleX = img.naturalWidth / imgDimensions.width;
            const scaleY = img.naturalHeight / imgDimensions.height;

            canvas.width = box.width * scaleX;
            canvas.height = box.height * scaleY;
            const ctx = canvas.getContext("2d")!;
            ctx.drawImage(
              img,
              box.x * scaleX,
              box.y * scaleY,
              box.width * scaleX,
              box.height * scaleY,
              0,
              0,
              canvas.width,
              canvas.height
            );

            // Example: Show cropped canvas
            const dataURL = canvas.toDataURL("image/png");
            const preview = new Image();
            preview.src = dataURL;
            document.body.appendChild(preview);
          });
        }}
      >
        Crop Images
      </button>
    </div>
  );
}
